<template>
  <div class="billRecord">
    <div class="billRecord-icon">
        <div class="billRecord-icon-item">
            <img src="../assets/food-active.png">
            <div>餐饮</div>
        </div>
        <div class="billRecord-icon-item">
            <img src="../assets/clothes.png">
             <div> 服饰</div>
        </div>
        <div class="billRecord-icon-item">
            <img src="../assets/shopping.png">
             <div>购物</div>
         </div>
        <div class="billRecord-icon-item">
            <img src="../assets/recreation.png">
             <div>娱乐</div>
          </div>
        <div class="billRecord-icon-item">
            <img src="../assets/heart.png">
             <div>人情</div>
        </div>
    </div>
    <div class="billRecord-sum">
        <div class="billRecord-sum-item" >
                <img src="../assets/food-active.png">
            <div>
                餐饮
            </div>
        </div>
        <div class="billRecord-sum-num">
                <font style="font-size:30px;font-weight:bold">{{sum}}</font>
                <div  style="overflow: auto;height:20px;"  ref="history">
                    <font >{{history}}</font>
                </div>
        </div>
    </div>
    <div class="billRecord-other">
            <div class="billKey billOtherMidden">备注</div>
            <div class="billKey billOther">今天</div>
    </div>
    <div class="billRecord-keyboard">
        <div class="billRecord-keyboard-num">
             <div class="billKey" v-for="item in items" :key="item" @click="clickNum(item)">{{item}}</div>
            <div class="billKey billKeyMidden" @click="clickNum(0)">0</div>
            <div class="billKey" @click="clickNum('.')">.</div>
        </div>
        <div class="billRecord-keyboard-sidebar">
            <div class="billKey billKeySidebar" @click="clickNum('+')">+</div>
            <div class="billKey billKeySidebar" @click="clickNum('-')">-</div>
            <div class="billKey billKeySidebar billKeySidebarMidden" :class="{'BtnSure':(btnDesc==='完成'), 'BtnCancle':(btnDesc==='取消')}">{{btnDesc}}</div>
        </div>
    </div>
  </div>
</template>

<script>
import {NumberKeyboard,Toast} from 'vant';
export default {
  components: {
      [NumberKeyboard.name]: NumberKeyboard,
       [Toast.name]: Toast,
  },
 name: 'billRecord',
 data() {
    return {
        show:true,
        items: [1,2,3,4,5,6,7,8,9],
        sum: "0.00",
        history:" ",
        btnDesc: "取消",
        isSure: true
    }
},
  props: {
    msg: String
  },
  watch: {
      sum : function(val){
          if (parseFloat(val) != 0)
          {
              this.$toast(parseFloat(val))
              this.isSure = true
              this.btnDesc = "完成"
          }else{
               this.$toast(parseFloat(val))
              this.isSure = false
              this.btnDesc = "取消"
          }
      }
  },
  methods: {
    clickNum(selNum){
        this.history +=selNum
        this.$refs.history.scrollLeft =this.$refs.history.scrollWidth
        this.compute(this.history)
    },
    compute(str){
        let oldSum = 0.00
        let NewNum = ""
        let lastChar = ""
       for (let i=0;i<str.length;i++)
       {   
           if ((str[i]==="+")&&(NewNum != ""))
           {
               NewNum = ""
               lastChar = "+"
           }else if((str[i] === "-")&&(NewNum != ""))
           {
               NewNum = ""
               lastChar = "-"
           }else{
               let lastNum = ""
               if ( NewNum.length > 0)
               {
                   lastNum = NewNum
               }
               NewNum += str[i]
              if (lastChar === "+"){
                   if (lastNum != "") oldSum -= parseFloat(lastNum)
                   oldSum += parseFloat(NewNum)
              }else if(lastChar==="-")
              {
                   if (lastNum != "") oldSum += parseFloat(lastNum)
                  oldSum -= parseFloat(NewNum)
              }else{
                  oldSum = parseFloat(NewNum)
              }
               
           }
       }
       this.sum = oldSum
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.billRecord{
    height: 300px;
    &-icon{
        display: flex;
        flex-direction: row;
        text-align: center;
        padding-top: 20px;
        margin-bottom: 20px;
        &-item{
            flex-grow: 1;
        }
    }
    &-sum{
        background: white;
        display: flex;
        flex-direction: row;
        padding-top: 5px;
        padding-bottom: 7px;
        &-item{
            text-align: center;
            width: 33.333333%;
            padding-left: 20px;
        }
        &-num{
            width: 66.666666%;
            display: flex;
            flex-direction: column;
            text-align: right;
            padding-right: 20px;
        }
    }
     &-other{
        display: flex;
        flex-direction: row;
    }
    &-keyboard{
        display: flex;
        flex-direction: row;
        &-sidebar{
            display: flex;
            flex-direction: column;
            flex-grow: 1;
        }
        &-num{
            flex-grow: 3;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
        }
    }
}
.billKey{
    width: 33.33333333%;
    font-size: 24px;
    font-style: normal;
    text-align: center;
    display: inline-block;
    vertical-align: middle;
    height: 54px;
    line-height: 54px;
    border: 0px solid #ebedf0;
    border-width: 1px 1px 0 0;
    background-color:#fff;
    box-sizing: border-box;

}
.billKeyMidden{
    width: 66.66666%;
}
.billKeySidebar{
    width: 100%;
}
.billKeySidebarMidden
{
    height: 108px;
     line-height: 108px;
}
.billKey:active{
    background: #BEBECA;
}
.billOtherMidden{
    width: 75%
}
.billOther{
    width: 25%;
}
.BtnCancle{
    background: #BEBECA;
}
.BtnSure{
    background: #FF69B4;
}
</style>
